$prefix: '.rise-blocks';

#{$prefix}-image-text-slider {

    display: flex !important;
    align-items: center;
    // justify-content: center;
    
    .slider-image-wrapper {
        flex: 0 0 60%;
        padding-right: 40px;
    }

    .slider-text-wrapper {
        flex: 0 0 40%;
        color: #fff;
        text-align: left;
    }

    .image-slider {
        display: block;
        width: 100%;
    }

    #{$prefix}-slider-arrow {
        position: absolute;
        top: 50%;
        transform: translate(0, -50%);
        background: white;
        border: none;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 40px;
        width: 40px;
        border-radius: 50%;
        font-size: 25px;
        line-height: 1;
        z-index: 99;
        padding: 0;
        transition: all .3s;
        cursor: pointer;

        &:hover {
            background-color: #FF8500;
        }

        &.rise-blocks-next-arrow  {
            right: 10px;
        }

        &.rise-blocks-prev-arrow  {
            left: 10px;
        }
    }

    img {
        display: block;       
        object-fit: cover; 
    }

    ul.slick-dots {
        position: absolute;
        bottom: 30px;
        display: flex !important;
        list-style: none;
        left: 62%;
        right: 0;
        margin: auto;
        justify-content: center;
        transform: translate(-50%, 0);

        li {
            padding: 5px;
            button {
                border: none;
                background: #000;
                height: 20px;
                width: 20px;
                border-radius: 50%;
                font-size: 0;
                cursor: pointer;
                padding: 0;
            }

            &.slick-active,
            &:hover {
                button {
                    background-color: #FF8500;
                }
            }
        }
    }
}
